@import '_base.sass';

//Mobile up to 1000px
@media only screen and (max-width: 1000px) {
    @include theme($theme: $color-theme-Darkgreen);
    main {
        background-image: url('../img/Full_Moon_on_August_22,_2021_-_Flickr_-_gjdonatiello.jpg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        length: 80vh;
        h1 {
            text-align: center;
            font-family: $font-heading;
            font-size: $font-size-large;
            -webkit-text-stroke: 1px #8d1d85;
            color: $font-accent-color;
            text-shadow: 0px 2px 15px rgba(255, 103, 252, 0.50); } } }
